<template>
  <h1>{{ number }}</h1>
</template>

<script>
export default {
  name: "countUp",
  props: ["number"],
  data(){
    return {
      countDown : 0
    }
  },
  watch:{
     /* countDown() {
        this.countDownTimer(); // or whatever you need to do
      } */
  },
  created(){
    //this.countDownTimer();
  },
  methods: {
      countDownTimer() {
          console.log('here',this.number);

          if(this.countDown < this.number) {
            var change = 1;
            if(this.number>500 && this.number<=1000){
              change=10
            }else if(this.number>1000 && this.number<=5000){
              change=100
            }
              setTimeout(() => {
                  this.countDown += change
                  this.countDownTimer()
              }, 10)
          }
      }
  },
        
};
</script>

